<template>
  <div v-if="pending">Loading...</div>
  <div v-else-if="error">{{ error.message }}</div>
  <div v-else-if="video">
    <h1>{{ video.title }}</h1>
    <p>By {{ video.author }}</p>
    <img :src="video.thumbnail_url" :alt="video.title">
    <div v-html="video.summary"></div>
  </div>
</template>

<script setup lang="ts">
import { type Video } from '@/types'

const route = useRoute()
const { data: video, pending, error } = await useFetch<Video>(`/api/videos/${route.params.id}`)
</script>